// src/app/buy/page.tsx
"use client";

import useSWR from "swr";
import Link from "next/link";
import Image from "next/image";
import { API_BASE } from "../../lib/config";

type AuctionCard = {
  id: number;
  title: string;
  description?: string | null;
  image?: string | null;
  images?: string[] | null;
  currentBid: number;
  endTime?: string | null;
  badge?: string | null;
  createdAt: string;
};

type AuctionsResponse = { items: AuctionCard[] };

const fetcher = async (url: string) => {
  const res = await fetch(url, {
    // GET is public; omit credentials
    headers: { Accept: "application/json" },
    cache: "no-store",
  });
  if (!res.ok) throw new Error(`${res.status} ${res.statusText}`);
  return res.json();
};

export default function BuyPage() {
  const { data, error, isLoading } = useSWR<AuctionsResponse>(
    // call relative URL so Next rewrites to backend
    API_BASE + "/api/auctions?sort=latest&limit=100",
    fetcher
  );

  if (isLoading) {
    return (
      <main className="max-w-6xl mx-auto p-4">
        <h1 className="text-2xl font-semibold mb-4">Auctions</h1>
        <p className="text-gray-500">Loading…</p>
      </main>
    );
  }

  if (error) {
    return (
      <main className="max-w-6xl mx-auto p-4">
        <h1 className="text-2xl font-semibold mb-4">Auctions</h1>
        <p className="text-red-600 text-sm">Failed to load auctions.</p>
      </main>
    );
  }

  const items = data?.items ?? [];

  return (
    <main className="max-w-6xl mx-auto p-4">
      <h1 className="text-2xl font-semibold mb-4">Auctions</h1>

      {items.length === 0 ? (
        <p className="text-gray-500">No auctions yet.</p>
      ) : (
        <ul className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
          {items.map((a) => (
            <li
              key={a.id}
              className="border rounded-xl overflow-hidden bg-white shadow-sm hover:shadow"
            >
              <Link href={`/auction/${a.id}`} className="block">
                <div className="relative w-full h-48 bg-gray-100">
                  <Image
                    src={a.image || "/placeholder.png"}
                    alt={a.title}
                    fill
                    className="object-cover"
                  />
                </div>
                <div className="p-3">
                  <h3 className="font-semibold line-clamp-1">{a.title}</h3>
                  <p className="text-sm text-gray-500 line-clamp-2">
                    {a.description || "—"}
                  </p>
                  <div className="mt-2 text-sm">
                    <span className="font-medium">Current:</span>{" "}
                    {formatLKR(a.currentBid)}
                  </div>
                  {a.endTime && (
                    <div className="text-xs text-gray-500">
                      Ends {new Date(a.endTime).toLocaleString()}
                    </div>
                  )}
                </div>
              </Link>
            </li>
          ))}
        </ul>
      )}
    </main>
  );
}

function formatLKR(n: number) {
  try {
    return new Intl.NumberFormat("en-LK", {
      style: "currency",
      currency: "LKR",
      maximumFractionDigits: 0,
    }).format(n);
  } catch {
    return `LKR ${Math.round(n).toLocaleString()}`;
  }
}
